import { useCallback, useEffect, useRef } from 'react';

interface UseDialogCloseTimerProps {
    container: HTMLElement;
    timeout?: number;
}

/**
 * @description This hook is used to hide a dialog window after a certain amount of time if the user doesn't interact with it
 * @param container - The outer most container of the dialog window
 * @param timeout - The timeout in milliseconds. Default is 3000
 *
 * @remark use in tandem with useHideWindowOnBlur hook, which calls the dialog window cancel callback when the window is blurred
 */
export const useDialogCloseTimer = ({ container, timeout = 3000 }: UseDialogCloseTimerProps) => {
    const timerRef = useRef<NodeJS.Timeout>();

    const stopTimer = useCallback(() => {
        clearTimeout(timerRef.current);
        timerRef.current = undefined;
    }, []);

    const startTimer = useCallback(() => {
        if (timerRef.current) {
            stopTimer();
        }
        timerRef.current = setTimeout(async () => {
            await fin.me.hide();
        }, timeout);
    }, [stopTimer, timeout]);

    const restartTimer = useCallback(() => {
        stopTimer();
        startTimer();
    }, [startTimer, stopTimer]);

    useEffect(() => {
        (async () => {
            await fin.Window.getCurrentSync().on('shown', startTimer);
        })();

        return () => {
            fin.Window.getCurrentSync().removeListener('shown', startTimer);
            stopTimer();
        };
    }, [startTimer, stopTimer]);

    useEffect(() => {
        if (container) {
            container.addEventListener('mouseenter', stopTimer);
            container.addEventListener('mouseleave', startTimer);
        }

        return () => {
            if (container) {
                container.removeEventListener('mouseenter', stopTimer);
                container.removeEventListener('mouseleave', startTimer);
            }
        };
    }, [container, startTimer, stopTimer]);

    return {
        restartTimer
    };
};
