import { useCallback, useEffect, useRef } from 'react';

import OpenFin from '@openfin/core';

export const useHideWindowOnBlur = (disable?: boolean, onHide?: () => void) => {
    const onWindowBlurRef = useRef<() => void>();

    onWindowBlurRef.current = useCallback(async () => {
        if (disable) {
            return;
        }

        onHide && onHide();
        await (fin.me as OpenFin.Window).hide();
    }, [disable, onHide]);

    useEffect(() => {
        const handleBlur = async () => {
            if (onWindowBlurRef.current) {
                onWindowBlurRef.current();
            }
        };

        (async () => {
            await (fin.me as OpenFin.Window).on('blurred', handleBlur);
            await (fin.me as OpenFin.Window).on('hidden', handleBlur);
        })();

        return () => {
            // TODO WRK-4956: Unawaited promises in react hook disposers can cause global rejections
            (fin.me as OpenFin.Window).removeListener('blurred', handleBlur);
            (fin.me as OpenFin.Window).removeListener('hidden', handleBlur);
        };
    }, []);
};
