import { FC, useCallback } from 'react';

import { withRouter } from 'next/router';

import OpenFin from '@openfin/core';

import { createZoomControlsDialogChannel } from '@common/utils/enterprise-channels';
import { getClosestBoundsIfModalBoundsOffscreen } from '@common/utils/modal-bounds';
import { ZoomControlsDialogChannelMessage } from '@common/utils/popup-window';
import { Router } from '@common/utils/router';
import { WindowName } from '@common/utils/window';

import { ZoomControlsDialog } from '@enterprise/components/App/ContextMenu/ZoomControlsDialog';
import BrowserThemeProvider from '@enterprise/components/App/ThemeProvider/BrowserThemeProvider';

import { BrowserTypeContext } from '@common/hooks/context';
import { useManagePopupWindow } from '@enterprise/hooks/useManagePopupWindow';

interface Props {
    router: Router;
}

export const ZoomControlsDialogPage: FC<Props> = () => {
    const showPopupOverride = useCallback(async (bounds: OpenFin.Bounds) => {
        const window = fin.me as OpenFin.Window;
        const isShowing = await window.isShowing();
        if (isShowing) {
            // If the window is already showing, we don't need to show it again
            return;
        }
        await window.setBounds(await getClosestBoundsIfModalBoundsOffscreen(bounds));
        await window.show();
        // do not call window.focus() here because user this action may have been invoked via keyboard shortcut
        // and we don't want to steal focus from that view
    }, []);

    const { channelRef, payload, showPopup, onResponse } = useManagePopupWindow<ZoomControlsDialogChannelMessage>(
        WindowName.ZoomControlsDialog,
        createZoomControlsDialogChannel,
        showPopupOverride,
        {
            dispatchDirectlyToChannel: true,
            forwardTopicName: 'zoom-controls-dialog-action'
        }
    );

    return (
        <BrowserThemeProvider>
            <BrowserTypeContext.Provider value={{ isEnterprise: true }}>
                <ZoomControlsDialog
                    channelRef={channelRef}
                    messageData={payload}
                    showPopup={showPopup}
                    onResponse={onResponse}
                />
            </BrowserTypeContext.Provider>
        </BrowserThemeProvider>
    );
};

export default withRouter(ZoomControlsDialogPage);
