import { OpenFin } from '@openfin/core';

export const ZoomUtils = {
    // Converts a zoom percentage (e.g., 125) to a Chromium/Here zoom level (e.g., ~0.73).
    percentToLevel(percent: number) {
        try {
            const p = Math.log(percent / 100) / Math.log(1.2);
            // Round to two decimal places
            return Math.round(p * 100) / 100;
        } catch (e) {
            console.error('Error converting percent to level:', e);
            // Default to 0 if there's an error
            return 0;
        }
    },
    // Converts a Chromium/Here zoom level (e.g., 1) to a zoom percentage (e.g., 120).
    levelToPercent(level: number) {
        try {
            const l = Math.pow(1.2, level);
            // Round to the nearest whole number
            return Math.round(l * 100);
        } catch (e) {
            console.error('Error converting level to percent:', e);
            // Default to 100% if there's an error
            return 100;
        }
    }
};

const ZOOM_CONTROLS_MAP = [25, 33, 50, 67, 75, 80, 90, 100, 110, 125, 150, 175, 200, 250, 300, 400, 500];
const MAX_ZOOM = ZOOM_CONTROLS_MAP[ZOOM_CONTROLS_MAP.length - 1];
const MIN_ZOOM = ZOOM_CONTROLS_MAP[0];

export async function zoomViewIn(view: OpenFin.View) {
    const currentZoom = ZoomUtils.levelToPercent(await view.getZoomLevel());

    if (currentZoom >= MAX_ZOOM) {
        return currentZoom;
    }

    const index = ZOOM_CONTROLS_MAP.indexOf(currentZoom);
    let nextZoomLevel: number;

    if (index === -1) {
        // if current zoom isnt in our interal list, find nearest zoom level
        nextZoomLevel = ZOOM_CONTROLS_MAP.find((zoom) => zoom > currentZoom);
    } else {
        nextZoomLevel = ZOOM_CONTROLS_MAP[index + 1];
    }

    await view.setZoomLevel(ZoomUtils.percentToLevel(nextZoomLevel));
    return nextZoomLevel;
}

export async function zoomViewOut(view: OpenFin.View) {
    const currentZoom = ZoomUtils.levelToPercent(await view.getZoomLevel());

    if (currentZoom <= MIN_ZOOM) {
        return currentZoom;
    }

    const index = ZOOM_CONTROLS_MAP.indexOf(currentZoom);
    let nextZoomLevel: number;

    if (index === -1) {
        for (let i = ZOOM_CONTROLS_MAP.length - 1; i >= 0; i--) {
            if (ZOOM_CONTROLS_MAP[i] < currentZoom) {
                nextZoomLevel = ZOOM_CONTROLS_MAP[i];
                break;
            }
        }
    } else {
        nextZoomLevel = ZOOM_CONTROLS_MAP[index - 1];
    }

    await view.setZoomLevel(ZoomUtils.percentToLevel(nextZoomLevel));
    return nextZoomLevel;
}

export async function resetViewZoom(view: OpenFin.View) {
    await view.setZoomLevel(ZoomUtils.percentToLevel(100));
}
